export default{
  methods: {
    // 对象的非空校验
    check: function check(obj, requireArr) {
      return new Promise((resolve, reject) => {
        this.lodash.some(requireArr, (item) => {
          if (!obj[item] && !this.lodash.isBoolean(obj[item])) reject();
        })
        resolve();
      })
    }
  },
  tips: {
    checkTip: '请补全信息后提交'
  }
}